import React, { useState, useEffect } from 'react';
import { Row, Col, Form, Button as BtnReact } from 'react-bootstrap';
import { useHistory } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import ImageUploader from 'react-image-upload';
import "react-image-upload/dist/index.css";
import { PolygonLogo, BSCLogo, ETHLogo, FantomLogo } from "../components/Chains/Logos";
import { DownOutlined } from "@ant-design/icons";
import { Menu, Dropdown, Button } from "antd";
import { useMoralisFile, useMoralis, useChain, useNewMoralisObject } from 'react-moralis';

import Web3 from "web3";
import { ethers } from 'ethers';
import {Contract} from '../helpers/contractInfo';
import '../assets/css/create.css';

const Create = () => {
    const [img, setImage] = useState();
    const [name, setName] = useState('');
    const history = useHistory();
    const [isProcess, setProcess] = useState(false);
    const [ royalty, setRoyal ] = useState(0);
    const [content, setContent] = useState('');
    const { saveFile, moralisFile } = useMoralisFile();
    const { save } = useNewMoralisObject("NFTs");
    const { switchNetwork } = useChain();
    const { user, Moralis, chainId, isAuthenticated } = useMoralis();
    const [selected, setSelected] = useState({
        key: "0x1",
        value: "Ethereum",
        icon: <ETHLogo />,
    });
    const web3 = new Web3(Web3.givenProvider);
    // const web3 = new ethers.providers.Web3Provider(window.ethereum)
    const createNFT = 'createArtwork';
    const [chainType, setChain] = useState("Ethereum");
    // useEffect(()=> {
    //     initialNet();
    // }, []);

    useEffect(() => {
        if(!isAuthenticated) history.push("/");
    }, []);

    useEffect(()=> {

        console.log("chainId", chainId)
        if (!chainId) return null;
        try {
            const newSelected = menuItems.find((item) => item.key === chainId);
            if(!newSelected) switchNetwork(selected.key);
            else {
                setSelected(newSelected);
                setChain(newSelected.value);
            }
        } catch(err) {
            alert(JSON.stringify(err));
            history.push('/');
        }
    }, [chainId])
    
    // const initialNet = async() => {
    //     const newSelected = menuItems.find((item) => item.key === chainId);
    //     try {
    //         if(!newSelected) await switchNetwork(menuItems[0].key);
    //         else {
    //             await switchNetwork(newSelected.key);
    //             setSelected(newSelected);
    //             setChain(newSelected.value);
    //         }
    //     } catch (err) {
    //         alert(err?.message?err?.message:err);
    //         history.push('/');
    //     }
    // }

    const handleMenuClick = async(e) => {
        const newSelected = menuItems.find((item) => item.key === e.key);
        try {
            await switchNetwork(newSelected.key);
            setSelected(newSelected);
            setChain(newSelected.value);
        } catch(err) {
            alert(err?.message?err?.message:err);
        }
        console.log(chainId)
    };

    const menuItems = [
        {
            key: "0x1",
            value: "Ethereum",
            icon: <ETHLogo />,
        },
        {
            key: "0x38",
            value: "Binance",
            icon: <BSCLogo />,
        },
        {
            key: "0x89",
            value: "Polygon",
            icon: <PolygonLogo />,
        },
        {
            key:"0xfa",
            value: "Fantom",
            icon: <FantomLogo/>
        }
    ];
    // const menuItems = [
    //     {
    //         key: "0x4",
    //         value: "Ethereum",
    //         icon: <ETHLogo />,
    //     },
    //     {
    //         key: "0x61",
    //         value: "Binance",
    //         icon: <BSCLogo />,
    //     },
    //     {
    //         key: "0x13881",
    //         value: "Mumbai",
    //         icon: <PolygonLogo />,
    //     },
    //     {
    //         key:"0xfa2",
    //         value: "Fantom",
    //         icon: <FantomLogo/>
    //     }
    // ];

    const styles = {
        item: {
          display: "flex",
          alignItems: "center",
          height: "42px",
          fontWeight: "500",
          fontFamily: "Roboto, sans-serif",
          fontSize: "14px",
          padding: "0 10px",
        },
        button: {
            border: "1px solid rgb(231, 234, 243)",
            borderRadius: "7px",
        },
    }

    const menu = (
        <Menu onClick={handleMenuClick}>
            {menuItems.map((item) => (
            <Menu.Item key={item.key} icon={item.icon} style={styles.item}>
            <span style={{ marginLeft: "5px" }}>{item.value}</span>
            </Menu.Item>
        ))}
        </Menu>
    );
    // const { isSaving, error, save } = useNewMoralisObject('UnikArt');
    const mintNFT = async () => {
        setProcess(true);
        if(!img || img == null) {
            alert("Please insert your NFT image")
            setProcess(false);
            return
        }
        if(!royalty) {
            alert("Please insert your royalty (%)")
            setProcess(false);
            return
        }
        try {
            const imageFile = await saveFile(img.file?.name, img.file, {saveIPFS: true})
            console.log(imageFile)
            if(!imageFile) {
                setProcess(false);
                return;
            }
            const imageURI = imageFile?._ipfs;
            const metadata = {
                name: name,
                description: content,
                image: imageURI
            }
            console.log(metadata);
            const metadataFile = await saveFile("metadata.json", {base64 : btoa(JSON.stringify(metadata))}, {saveIPFS: true});
            const metadataURI = metadataFile?._ipfs;
            console.log(metadataURI, imageURI)
            createArt(metadataURI, imageURI)
        } catch (err) {
            console.log(err)
            alert(err?.message?err?.message:err);
            setProcess(false);
        }
    }

    const createArt = async (metaUrl, imageURI) => {
        const ops = {
            contractAddress: Contract[chainId],
            functionName: createNFT,
            abi: Contract.contractAbi,
            params : {
                uri: metaUrl,
                royalty: royalty,
            }
        };
        try {

            // const con = new web3.eth.Contract(Contract.contractAbi, Contract[chainId], {gas:6721975});
            const con = new web3.eth.Contract(Contract.contractAbi, Contract[chainId]);
            // const provider  = new ethers.providers.JsonRpcProvider();
            // const signer = provider.getSigner(0);
            // console.log("signer", signer)
            // const con = new ethers.Contract(Contract[chainId], Contract.contractAbi, signer)
            var gasPrice = await web3.eth.getGasPrice();
            // var gasPrice = await ethers.getDefaultProvider().getGasPrice();

            console.log("current gas price is: ", gasPrice*1.1)
            console.log("con", con, metaUrl, chainId)
            // let receiptObj = await con.methods.createArtwork(metaUrl, royalty);
            // console.log("receiptObj", receiptObj)
            // let receipt =  await receiptObj.send({from: user.attributes.ethAddress})
            // let receipt; 
            let receipt = await con.methods.createArtwork(metaUrl, royalty).send({from: user.attributes.ethAddress, gasPrice: Math.round(gasPrice * 1.1)});
            // if(chainId === "0xfa") {
            //     receipt = await con.methods.createArtwork(metaUrl, royalty).send({from: user.attributes.ethAddress, gasPrice: 100000000000});
            // } else if(chainId === "0x89") {
            //     receipt = await con.methods.createArtwork(metaUrl, royalty).send({from: user.attributes.ethAddress, gasPrice: 100000000000});
            // } else if (chainId === "0x38") {
            //     receipt = await con.methods.createArtwork(metaUrl, royalty).send({from: user.attributes.ethAddress, gasPrice: 10000000000});
            // } else {
            //     receipt = await con.methods.createArtwork(metaUrl, royalty).send({from: user.attributes.ethAddress, gasPrice: 20000000000});
            // }
            console.log("receipt", receipt)
            let tokenId = receipt.events.Transfer.returnValues.tokenId;
            console.log("tokenId", tokenId)
            const tokenAddress = await con.methods.ownerOf(tokenId);
            console.log("tokenAddress", tokenAddress)
            if(tokenId)
                save2DB(metaUrl, tokenId, imageURI);
        } catch(err) {
            alert(err?.message?err?.message:err);
            console.log(err);
            setProcess(false);
        }
        // await contract.runContractFunction({
        //     params: ops,
        //     onSuccess: (data) => {
        //       console.log("success", data);
        //     },
        //     onError: (error) => {
        //       console.log(error, 'error')
        //     }
        // })
        // if(error) {
        //     alert(error)
        // }
        // else {
        //     // save2DB(metaUrl, data)
        // }
    }

    const save2DB = async (metaUrl, nftId, imageURI) => {
        console.log(chainType);
        const data = {
            'name': name,
            'desciption': content,
            'imgpath': imageURI,
            'metadataPath': metaUrl,
            'royalty': Number(royalty),
            'nftId': nftId,
            'creator': user.attributes.ethAddress,
            'currentOwner': user.attributes.ethAddress,
            'contractAddress': Contract[chainId],
            'likes': 0,
            'ChainType': chainType,
            'preSale': false,
            'active': false,
            'price':0,
            'sellType': '',
            'likers': []
        }
        await save(data);
        alert("NFT is minted!");
        setProcess(false);
    }

    return (
        <>
            <div className='create-main'>
                <br/><br/><br/>
                <Row className="mx-0">
                    <Col>
                        <ImageUploader
                            style={{ height: 350, width: 350, background: 'white', border:'2px dashed black', borderRadius: 13 }}
                            deleteIcon={
                                <img
                                src='https://img.icons8.com/ios-glyphs/30/000000/delete-sign.png'
                                alt=''
                                />
                            }
                            uploadIcon={
                                <svg
                                className='svg-circleplus'
                                viewBox='0 0 100 100'
                                style={{ height: '40px', stroke: '#000' }}
                                >
                                <circle cx='50' cy='50' r='45' fill='none' strokeWidth='7.5'></circle>
                                <line x1='32.5' y1='50' x2='67.5' y2='50' strokeWidth='5'></line>
                                <line x1='50' y1='32.5' x2='50' y2='67.5' strokeWidth='5'></line>
                                </svg>
                            }
                            onFileAdded={(img) => setImage(img)}
                        />
                    </Col>
                    <Col>
                        <Form>
                            <Form.Group className="mb-3">
                                <Form.Label>Name</Form.Label>
                                <Form.Control type="text" onChange={e => setName(e.target.value)} required />
                            </Form.Group>
                            <Form.Group className="mb-3">
                                <Form.Label>Description</Form.Label>
                                <Form.Control as="textarea" rows={5} required onChange={e => setContent(e.target.value)} />
                            </Form.Group>
                            <Row>
                                <Col md={10}>
                                    <Form.Label>Royalty (%)</Form.Label>
                                    <Form.Range onChange={e => setRoyal(e.target.value)} defaultValue={0} min={0} max={100} />
                                </Col>
                                <Col md={2}>
                                    <br/>
                                    <Form.Control type="number" value={royalty} />
                                </Col>
                            </Row>
                            <Dropdown overlay={menu} trigger={["click"]}>
                                <Button
                                key={selected?.key}
                                icon={selected?.icon}
                                style={{ ...styles.button, ...styles.item }}
                                >
                                <span style={{ marginLeft: "5px" }}>{selected?.value}</span>
                                <DownOutlined />
                                </Button>
                            </Dropdown>
                            {/* <Form.Text>
                            lll are charged to create a new NFT.
                            </Form.Text> */}
                            <br/><br/>
                            <BtnReact variant="primary" style={{float:'right'}} onClick={mintNFT} disabled={isProcess}>{ isProcess? "Processing..." : "Mint NFT"}</BtnReact>
                        </Form>
                    </Col>
                </Row>
            </div>
        </>
    )
}

export default Create;